.ddb-related-materials {
    position: relative;
    max-width: 1310px;
    min-height: 200px;
    background: #e5e5e5;
    padding: 16px 0;
}

@media (min-width: 672px) {
    .ddb-related-materials {
        padding-bottom: 0;
    }
}

.ddb-related-materials__title {
    margin-left: 16px;
}

.ddb-related-materials__search {
    text-decoration: none;
    color: inherit;
    margin-left: 16px;
}

.ddb-related-materials__search:hover, .ddb-related-materials__search:focus {
    text-decoration: underline;
}

@media (min-width: 672px) {
    .ddb-related-materials__search {
        position: absolute;
        top: 0;
        right: 0;
        margin-left: 0;
        margin-top: 16px;
        margin-right: 16px;
        text-align: right;
    }
}

.ddb-related-materials__list {
    list-style: none;
    display: flex;
    flex-wrap: wrap;
}

.ddb-related-material {
    display: flex;
    height: 210px;
    width: 147px;
    margin: 16px;
    overflow: hidden;
    border-radius: 5px;
}

.ddb-related-material:focus {
    outline: 2px solid #808285;
    outline-offset: 2px;
}

@media (min-width: 1056px) {
    .ddb-related-material {
        height: 330px;
        width: 230px;
    }
}

.ddb-related-material img {
    object-fit: cover;
    height: 100%;
    width: 100%;
    opacity: 0;
    transition: all 240ms cubic-bezier(0.4, 0.14, 0.3, 1);
    background: #ccc;
}

.ddb-related-material--finished img {
    opacity: 1;
}

.ddb-related-material:hover img {
    transform: scale(1.1);
}
